
import { jsonp } from '../assets/js/common.js';
import Card from 'vux/src/components/card/index.vue'
import Previewer from 'vux/src/components/previewer/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import CellBox from 'vux/src/components/cell-box/index.vue'


export default {
created() {
jsonp(`/subject/${this.$route.params.id}`).then(res => {
console.log(res);
this.previewerList = [{
w: 300,
h: 419,
src: res.images.large
}];
this.dataList = res;
this.dataList.anotherName = res.aka.join('、');
this.dataList.countries = res.countries.join('、');
this.dataList.genres = res.genres.join('、');
}).catch(ex => {
console.error(ex);
});
},
methods: {
show() {
this.$refs.previewer.show(0);
}
},
data() {
return {
dataList: [],
previewerList: [],
options: {
getThumbBoundsFn(index) {

let thumbnail = document.querySelectorAll('.previewer-demo-img')[index]

let pageYScroll = window.pageYOffset || document.documentElement.scrollTop


let rect = thumbnail.getBoundingClientRect()

return { x: rect.left, y: rect.top + pageYScroll, w: rect.width }


}
}
}
},
components: {
Card,
Previewer,
CellBox
},
directives: {
TransferDom
},
}



